import React from "react"
import PropTypes from "prop-types"
import "./index.css"

import Header from "./header"

const Layout = ({ children }) => (
  <>
    <Header />
    <div className="container mx-auto">
      <main>{children}</main>
    </div>
  </>
)

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
